import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { DirectPurchaseDetailMasterComponent } from './direct-purchase-detail-master.component';
import { DirectPurchaseDetailMasterAddComponent } from './direct-purchase-detail-master-add/direct-purchase-detail-master-add.component';
import { DirectPurchaseDetailMasterEditComponent } from './direct-purchase-detail-master-edit/direct-purchase-detail-master-edit.component';
import { DirectPurchaseDetailMasterRoutingModule } from './direct-purchase-detail-master-routing.module';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    DirectPurchaseDetailMasterRoutingModule
  ],
  declarations: [
    DirectPurchaseDetailMasterComponent, 
    DirectPurchaseDetailMasterAddComponent, 
    DirectPurchaseDetailMasterEditComponent
  ],
  entryComponents: [
    DirectPurchaseDetailMasterAddComponent,
    DirectPurchaseDetailMasterEditComponent
  ],
  schemas:[NO_ERRORS_SCHEMA]
})
export class DirectPurchaseDetailMasterModule { }



