import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DirectPurchaseDetailMasterComponent } from './direct-purchase-detail-master.component';

const routes: Routes = [
    {
        path: '', 
        component: DirectPurchaseDetailMasterComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DirectPurchaseDetailMasterRoutingModule { }


