import { Component, OnInit, ViewChild, Inject, ComponentFactoryResolver, ElementRef, ViewContainerRef } from '@angular/core';
import { routerTransition } from '../../../router.animations';
import { jqxWindowComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxwindow';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { PoRequisitionDetailMasterService, QuickaccessComponent, CurrentUserService } from '../../../shared';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { DirectPurchaseDetailMasterAddComponent } from './direct-purchase-detail-master-add/direct-purchase-detail-master-add.component';
import { DirectPurchaseDetailMasterEditComponent } from './direct-purchase-detail-master-edit/direct-purchase-detail-master-edit.component';
// import { DirectPurchaseDetailMasterViewComponent } from './direct-purchase-detail-master-view/direct-purchase-detail-master-view.component';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';

@Component({
  selector: 'app-direct-purchase-detail-master',
  templateUrl: './direct-purchase-detail-master.component.html',
  styleUrls: ['./direct-purchase-detail-master.component.scss']
})
export class DirectPurchaseDetailMasterComponent implements OnInit {
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @ViewChild('msgNotification') msgNotification: jqxNotificationComponent;
  @ViewChild('myGrid') myGrid: jqxGridComponent;
  @ViewChild('myWindow') myWindow: jqxWindowComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('Insert', { read: ViewContainerRef }) Insert: ViewContainerRef;
  @ViewChild('quickAccess', { read: ViewContainerRef }) quickAccess: ViewContainerRef;

  editrow: number = -1;
  apiUrl: string;
  source: any;
  dataAdapter: any;
  columns: any[];
  report: boolean = false;
  request_url: any;

  userData: any = {};

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    @Inject('API_URL') apiUrl: string,
    private prdm: PoRequisitionDetailMasterService,
    private cus: CurrentUserService,
  ) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData();
    this.getPoRequisitionDetail();
  }


  ngOnInit() {
    this.createQuickAccess();
  }
  createQuickAccess() {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(QuickaccessComponent);
    this.quickAccess.clear();
    const dyynamicComponent = this.quickAccess.createComponent(componentFactory);
  }
  getPoRequisitionDetail() {
    let myUrl = this.apiUrl + 'po_requisition_detail';
    this.source =
      {
        datatype: 'json',
        datafields: [
          { name: 'id', type: 'string' },
          { name: 'po_no', type: 'string' },
          { name: 'po_date', type: 'date' },
          { name: 'extra_ref', type: 'string' },
          { name: 'type', type: 'string' },
          { name: 'valid_up_to', type: 'date' },
          { name: 'store_name', type: 'string', map: 'store>name' },
          { name: 'vendor_name', type: 'string', map: 'vendor>name' },
          { name: 'store_id', type: 'string' },
          { name: 'vendor_id', type: 'string' }
        ],
        id: 'id',
        root: 'data',
        pagesize: 20,
        url: myUrl,
        totalrecords: 100,
        data: this.userData,
        filter: () => {
          // update the grid and send a request to the server.
          this.myGrid.updatebounddata('filter');
        },
        sort: () => {
          // update the grid and send a request to the server.
          this.myGrid.updatebounddata('sort');
        },
        beforeprocessing: (data) => {

        }
      }

    this.dataAdapter = new jqx.dataAdapter(this.source, {
      formatData: (data) => {
      
        if (this.report) {
          data['limit'] = 'all';
          data['report'] = true;
          data['report_type'] = 'direct_purchase_detail-report';
          data['type'] = 'dpo';
        } else {
          data['page'] = data['pagenum'] + 1;
          data['limit'] = data['pagesize'];
          data['type'] = 'dpo';
        }
        return data;
      },
      beforeSend: (jqXHR, settings) => {
        if (this.report) {
          this.request_url = settings.url;
        }
      }, downloadComplete: (data, status, xhr) => {
        this.source.totalrecords = data.data.total;
        let tData = data.data.data;
        return tData;
      }, loadError: (jqXHR, status, error) => {
        if (this.report) {
          this.report = false;
          let anchor = document.createElement('a');
          anchor.style.cssText = 'display: none';
          document.body.appendChild(anchor); // Attach to document
          anchor.href = this.request_url;
          anchor.click();
          setTimeout(()=>{
            this.myGrid.updatebounddata();
          },100);
        
        }else{
          let messageDiv: any = document.getElementById('listingMessage');
          messageDiv.innerText = error;
          this.winNotification.open();
          this.jqxLoader.close();
        }
      },bindingComplete:()=>{
        this.myGrid.updatebounddata();
      }
    });

    this.columns =
      [
        {
          text: 'S.N', sortable: false, filterable: false, editable: false,
          groupable: false, draggable: false, resizable: false,
          datafield: 'id', columntype: 'number', width: 50,
          cellsrenderer: function (row, column, value) {
            return "<div style='margin:4px;'>" + (value + 1) + "</div>";
          }
        },
        { text: 'Date', datafield: 'po_date', filtertype: 'range', width: 200, cellsformat: 'yyyy-MM-dd' },
        { text: 'Purchase Number', datafield: 'po_no', columntype: 'textbox', filtercondition: 'starts_with', width: 150 },
        { text: 'Type', datafield: 'type', columntype: 'textbox', filtercondition: 'starts_with', width: 175 },
        { text: 'Valid Upto', datafield: 'valid_up_to', sortable: false, filterable: false, width: 175, cellsformat: 'yyyy-MM-dd' },
        { text: 'Store', datafield: 'store_id', displayfield: 'store_name', columntype: 'textbox', filtertype: 'list', width: 175 },
        { text: 'Vendor', datafield: 'vendor_id', displayfield: 'vendor_name', columntype: 'textbox', filtertype: 'list', width: 200 },
        { text: 'Extra Reference', datafield: 'extra_ref', hidden: 'true', columntype: 'textbox', filtercondition: 'starts_with', width: 175 },
        // {
        //   text: 'Req Item Details', datafield: 'view_item_details', sortable: false, filterable: false, columntype: 'button', width: 150,
        //   cellsrenderer: (): string => {
        //     return 'View Item Details';
        //   },
        //   buttonclick: (row: number): void => {
        //     this.editrow = row;
        //     let dataRecord = this.myGrid.getrowdata(this.editrow);
        //     this.myWindow.draggable(true);
        //     this.myWindow.title('&nbsp;');
        //     const componentFactory = this.componentFactoryResolver.resolveComponentFactory(PoItemDetailsViewComponent);
        //     this.Insert.clear();
        //     const dynamic_Component = this.Insert.createComponent(componentFactory);
        //     this.myWindow.open();
        //     (<PoItemDetailsViewComponent>dynamic_Component.instance).RequisitionDetailViewData = dataRecord;

        //   }
        // },
        {
          text: 'Actions', datafield: 'Edit', sortable: false, filterable: false, columntype: 'button',
          cellsrenderer: (): string => {
            return 'Edit';
          },
          buttonclick: (row: number): void => {
            this.editrow = row;
            let dataRecord = this.myGrid.getrowdata(this.editrow);
            this.myWindow.draggable(true);
            this.myWindow.title('Edit Direct Purchase Detail');
            this.myWindow.open();
            const componentFactory = this.componentFactoryResolver.resolveComponentFactory(DirectPurchaseDetailMasterEditComponent);
            this.Insert.clear();
            const dynamic_Component = this.Insert.createComponent(componentFactory);
            (<DirectPurchaseDetailMasterEditComponent>dynamic_Component.instance).PoEditData = dataRecord;
            (<DirectPurchaseDetailMasterEditComponent>dynamic_Component.instance).UpdatedData.subscribe(result => {
              if (result.result == true) {
                let messageDiv = document.getElementById('message');
                messageDiv.innerText = result['message'];
                this.msgNotification.open();
                this.myWindow.hide();
                this.myGrid.updatebounddata('cells');
                dynamic_Component.destroy();
              }
            });

            (<DirectPurchaseDetailMasterEditComponent>dynamic_Component.instance).cancelData.subscribe(result => {
              if (result == true) {
                this.myWindow.hide();
                dynamic_Component.destroy();
              }
            });
          }
        }
      ];
  }
  rendergridrows = (params: any): any[] => {
    return params.data;
  }
  rendertoolbar = (toolbar: any): void => {
    let container = document.createElement('div');
    container.style.margin = '5px';

    let buttonContainer1 = document.createElement('div');
    let buttonContainer2 = document.createElement('div');
    let buttonContainer3 = document.createElement('div');
    let buttonContainer4 = document.createElement('div');
    let buttonContainer5 = document.createElement('div');
    let buttonContainer6 = document.createElement('div');

    buttonContainer1.id = 'buttonContainer1';
    buttonContainer3.id = 'buttonContainer3';
    buttonContainer5.id = 'buttonContainer5';
    buttonContainer6.id = 'buttonContainer6';

    buttonContainer1.style.cssText = 'float: left';
    buttonContainer3.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer5.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer6.style.cssText = 'float: left; margin-left: 5px';

    container.appendChild(buttonContainer1);
    container.appendChild(buttonContainer3);
    container.appendChild(buttonContainer5);
    container.appendChild(buttonContainer6);
    toolbar[0].appendChild(container);


    let addRowButton = jqwidgets.createInstance('#buttonContainer1', 'jqxButton', { width: 105, value: 'Add New Row', theme:'energyblue' });
    let deleteRowButton = jqwidgets.createInstance('#buttonContainer3', 'jqxButton', { width: 150, value: 'Delete Selected Row', theme:'energyblue' });
    let resetSearchdata = jqwidgets.createInstance('#buttonContainer5', 'jqxButton', { width: 150, value: 'Reset Search Data', theme:'energyblue' });
    let generateReport = jqwidgets.createInstance('#buttonContainer6', 'jqxButton', { width: 150, value: 'Generate Report', theme: 'energyblue' });

    addRowButton.addEventHandler('click', () => {
      this.myWindow.draggable(true);
      this.myWindow.title('Add Direct Purchase Details');
      this.myWindow.open();
      const componentFactory = this.componentFactoryResolver.resolveComponentFactory(DirectPurchaseDetailMasterAddComponent);
      this.Insert.clear();
      const dynamic_Component = this.Insert.createComponent(componentFactory);
      (<DirectPurchaseDetailMasterAddComponent>dynamic_Component.instance).AddedData.subscribe(result => {
        if (result.result == true) {
          let messageDiv = document.getElementById('message');
          messageDiv.innerText = result['message'];
          this.msgNotification.open();
          this.myWindow.hide();
          this.myGrid.updatebounddata('cells');
          dynamic_Component.destroy();

        }
      });

      (<DirectPurchaseDetailMasterAddComponent>dynamic_Component.instance).cancelData.subscribe(result => {
        if (result == true) {
          this.myWindow.hide();
          dynamic_Component.destroy();
        }
      });
    })
    resetSearchdata.addEventHandler('click', () => {
      this.myGrid.clearfilters();
    });
    generateReport.addEventHandler('click', () => {
      this.report = true;
      this.myGrid.updatebounddata();
    });
    deleteRowButton.addEventHandler('click', () => {
      let id = this.myGrid.getselectedrowindexes();
      let ids = [];
      let rowscount = this.myGrid.getdatainformation().rowscount;
      for(let i=0; i<id.length; i++){
        let dataRecord = this.myGrid.getrowdata(Number(id[i]));
        ids.push(dataRecord.id);
      }
      if (ids.length > 0 && ids.length <= parseFloat(rowscount)) {
        if (confirm("Are you sure? You Want to delete")) {
          // let id = this.myGrid.getrowid(selectedrowindex);
           this.jqxLoader.open();            
          this.prdm.destroy('['+ids+']').subscribe(data => {
            let messageDiv = document.getElementById('message');
            messageDiv.innerText = data['message'];
            this.msgNotification.open();
            this.jqxLoader.close();
            this.myGrid.updatebounddata('cells');   
          })
        }
      }else{
           let messageDiv = document.getElementById('message');
            messageDiv.innerText = 'Please select some item to delete';
            this.msgNotification.open();
      }
    })
  };

}



